import request from '../utils/request';

/**
 * 
 *  :  ,  id, 
 *  : id:  id
 *  : /artist/detail
 *  : /artist/detail?id=11972054 (Billie Eilish)
 */
export function getSongerDetail(id) {
  return request({
    url: '/artist/detail',
    method: 'get',
    params: {
      id,
    },
  });
}
