import request from "../utils/request";
/**
 * 
 * - phone: 
 * - password: 
 * - countrycode: 1
 * - md5_password: md5, password 
 * - captcha: , /captcha/sent,,, password 
 * @param {Object} params
 * @param {string} params.phone
 * @param {string} params.password
 * @param {string=} params.countrycode
 * @param {string=} params.md5_password
 * @param {string=} params.captcha
 */
export function loginWithPhone(params) {
  return request({
    url: "/login/cellphone",
    method: "post",
    params,
  });
}

/**
 * 
 * - email: 163 
 * - password: 
 * - md5_password: md5, password 
 * @param {Object} params
 * @param {string} params.email
 * @param {string} params.password
 * @param {string=} params.md5_password
 */
export function loginWithEmail(params) {
  return request({
    url: "/login",
    method: "post",
    params,
  });
}

/**
 * key
 */
export function loginQrCodeKey() {
  return request({
    url: "/login/qr/key",
    method: "get",
    params: {
      timestamp: new Date().getTime(),
    },
  });
}

/**
 * 
 * : keybase64,
 * base64,
 * @param {Object} params
 * @param {string} params.key
 * @param {string=} params.qrimg base64
 */
export function loginQrCodeCreate(params) {
  return request({
    url: "/login/qr/create",
    method: "get",
    params: {
      ...params,
      timestamp: new Date().getTime(),
    },
  });
}

/**
 * 
 * : ,800,801,802,803(803cookies)
 * @param {string} key
 */
export function loginQrCodeCheck(key) {
  return request({
    url: "/login/qr/check",
    method: "get",
    params: {
      key,
      timestamp: new Date().getTime(),
    },
    isHideLoading: true,
  });
}

/**
 * 
 *  :  , 
 * -  : /login/refresh
 */
export function refreshCookie() {
  return request({
    url: "/login/refresh",
    method: "post",
  });
}

/**
 * 
 *  :  , 
 */
export function logout() {
  return request({
    url: "/logout",
    method: "post",
  });
}
