/**
 * 
 :  ,, 
 : phone: 
 : ctcode: ,86
 : /captcha/sent
 : /captcha/sent?phone=13xxx} uid 
 * 
 */
export function sendSmsCode(phone) {
  return request({
    url: '/captcha/sent',
    method: 'get',
    params: {
      phone,
      timestamp: new Date().getTime(),
    },
  });
}

